import React from "react";
import { e, evaluate } from "mathjs";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: "0",
      operation: [],
      isCalculated: false,
    };
    this.handleNumberClick = this.handleNumberClick.bind(this);
    this.handleOperatorClick = this.handleOperatorClick.bind(this);
    this.handleDecimalClick = this.handleDecimalClick.bind(this);
    this.resetCalc = this.resetCalc.bind(this);
    this.calculator = this.calculator.bind(this);
  }

  calculator(e) {
    const displayOperation = this.state.operation.join("");
    var result = 0;
    try {
      result = evaluate(displayOperation);
    } catch (exception) {}
    if (!this.state.isCalculated) {
      this.setState({
        value: result,
        operation: [this.state.operation.join("") + "=" + result],
        isCalculated: true,
      });
    }
  }

  handleNumberClick(e) {
    this.setState((state) => {
      var operation = [];
      if (
        !state.isCalculated &&
        !isNaN(this.state.value) &&
        parseInt(this.state.value) !== 0
      ) {
        // state.value es un numero
        operation = state.operation.concat(e.target.value);
        return {
          value: state.value + e.target.value,
          operation,
        };
      }
      // state.value era un operador o igual a 0
      else {
        if (state.isCalculated) {
          operation = operation.concat(e.target.value);
        } else if (e.target.value !== "0") {
          operation = state.operation.concat(e.target.value);
        }
        return {
          value: e.target.value,
          operation,
          isCalculated: false,
        };
      }
    });
  }

  handleOperatorClick(e) {
    this.setState((state) => {
      var operation = [];
      if (state.isCalculated) {
        //si isCalculated es true, es porque lo ultimo que has hecho es darle al igual
        operation = operation.concat([state.value, e.target.value]);
      } else {
        operation = state.operation.concat(e.target.value);
      }

      if (isNaN(state.value)) {
        operation = [...state.operation];
        if (e.target.value !== "-") {
          while (
            operation.length > 0 &&
            isNaN(operation[operation.length - 1])
          ) {
            operation.pop();
          }
        }
        operation = operation.concat(e.target.value);
      }

      return {
        value: e.target.value,
        operation,
        isCalculated: false,
      };
    });
  }

  handleDecimalClick(e) {
    this.setState((state) => {
      var operation = [];
      if (state.value.search("\\.") === -1) {
        //si no hay ningun punto en el string
        operation = state.operation.concat(e.target.value);
        return {
          value: state.value + e.target.value,
          operation,
        };
      }
    });
  }

  resetCalc() {
    this.setState({
      value: 0,
      operation: [],
      isCalculated: false,
    });
  }
  render() {
    return (
      <div>
        <div className="container">
          <div className="screen-container">
            <div className="row">
              <div className="upper-screen col-12 bg-dark text-truncate">
                {this.state.operation}
              </div>
            </div>
            <div className="row">
              <div
                id="display"
                className="lower-screen col-12 bg-dark text-truncate"
              >
                {this.state.value}
              </div>
            </div>
          </div>
          <div className="row">
            <button
              type="button"
              id="clear"
              className="col-6 btn-lg btn-danger"
              onClick={this.resetCalc}
            >
              AC
            </button>
            <button
              type="button"
              id="divide"
              className="col-3 btn-lg btn-light"
              value="/"
              onClick={this.handleOperatorClick}
            >
              /
            </button>
            <button
              type="button"
              id="multiply"
              className="col-3 btn-lg btn-light"
              value="*"
              onClick={this.handleOperatorClick}
            >
              *
            </button>
          </div>
          <div className="row">
            <button
              type="button"
              id="seven"
              className="col-3 btn-lg btn-secondary"
              value="7"
              onClick={this.handleNumberClick}
            >
              7
            </button>
            <button
              type="button"
              id="eight"
              className="col-3 btn-lg btn-secondary"
              value="8"
              onClick={this.handleNumberClick}
            >
              8
            </button>
            <button
              type="button"
              id="nine"
              className="col-3 btn-lg btn-secondary"
              value="9"
              onClick={this.handleNumberClick}
            >
              9
            </button>
            <button
              type="button"
              id="subtract"
              className="col-3 btn-lg btn-light"
              value="-"
              onClick={this.handleOperatorClick}
            >
              -
            </button>
          </div>
          <div className="row">
            <button
              type="button"
              id="four"
              className="col-3 btn-lg btn-secondary"
              value="4"
              onClick={this.handleNumberClick}
            >
              4
            </button>
            <button
              type="button"
              id="five"
              className="col-3 btn-lg btn-secondary"
              value="5"
              onClick={this.handleNumberClick}
            >
              5
            </button>
            <button
              type="button"
              id="six"
              className="col-3 btn-lg btn-secondary"
              value="6"
              onClick={this.handleNumberClick}
            >
              6
            </button>
            <button
              type="button"
              id="add"
              className="col-3 btn-lg btn-light"
              value="+"
              onClick={this.handleOperatorClick}
            >
              +
            </button>
          </div>
          <div className="row">
            <div className="col-9 p-0">
              <button
                type="button"
                id="one"
                className="col-4 btn-lg btn-secondary"
                value="1"
                onClick={this.handleNumberClick}
              >
                1
              </button>
              <button
                type="button"
                id="two"
                className="col-4 btn-lg btn-secondary"
                value="2"
                onClick={this.handleNumberClick}
              >
                2
              </button>
              <button
                type="button"
                id="three"
                className="col-4 btn-lg btn-secondary"
                value="3"
                onClick={this.handleNumberClick}
              >
                3
              </button>
              <br />
              <button
                type="button"
                id="zero"
                className="col-8 btn-lg btn-secondary"
                value="0"
                onClick={this.handleNumberClick}
              >
                0
              </button>
              <button
                type="button"
                id="decimal"
                className="col-4 btn-lg btn-secondary"
                value="."
                onClick={this.handleDecimalClick}
              >
                .
              </button>
            </div>
            <div className="col-3 p-0">
              <button
                id="equals"
                type="button"
                className="col-12 btn-lg btn-muted equal-button"
                style={{ paddingTop: 33, paddingBottom: 33 }}
                onClick={this.calculator}
              >
                =
              </button>
            </div>
          </div>
        </div>
        <div className="text-center">
          <br />
          Made with ðŸ˜­ and ðŸ’¢ <br />
          from @7ing7ing ðŸ¤£
        </div>
      </div>
    );
  }
}

export default App;
